// Base

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
}

body {
  padding: 0;  
  margin: 0;
  color: var(--base-txt-color);
  font-family: var(--base-font-family);
  font-weight: 300;
  background: var(--body-bg);
  height: 100vh;
}

// HEADINGS --------------------------------------------------------------------------------------------
h1,h2,h3,h5,h6 {
  margin: 0 0 .5em;
  color: var(--heading-color);
}

// LINKS -----------------------------------------------------------------------------------------------
a {
  color: var(--link-color);
}

// APP SPLASH SCREEN -----------------------------------------------------------------------------------
.app-splash {
  position: fixed;
  background: #fff;
  color: #777;
  display: flex;
  left: 0;
  top:0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  text-align: center;
  .app-logo {
    width: 11vw;
    display: block;
    margin-bottom: 1em;
  }
}

// HELPER CLASSES -----------------------------------------------------------------------------------------
.calign {
  text-align: center;
}

.large-text {
  font-size: 1.6em;
}

.brand-color {
  color: var(--brand-color);
}

// GENERIC-ENTRY ------------------------------------------------------------------------------------------
.generic-entry {
  box-shadow: 0px 3px 5px rgba(0,0,0,0.07);
  border-radius: 5px;
  margin-bottom: .5em;
  padding: 1em;
  background: #fff;
}

// ROOT --------------------------------------------------------------------------------------------------
#root {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;  
  overflow: hidden;
}

// PAGE --------------------------------------------------------------------------------------------------
.page-content {
  position: absolute;
  top: var(--app-header-height);
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  padding: 1em;
  &.no-pad {
    padding: 0;
  }
}

.page-centered {
  display: flex;
  align-items: center;
  justify-content: center;
}

// SIGN IN / UP PAGES -------------------------------------------------------------------------------
.signup-box {  
  transform: translate(0px, 0px);
  opacity: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-h1 {
  justify-content: center;
  font-size: 1.7em;
}

.signin-box {
  transform: translate(0px, 0px);
  opacity: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submit-btn::part(base) {
  border-color: 1px solid black;
  background-color: #FEC76C;
  color: white;
  font-family: var(--base-font-family);
  font-size: 1.1em;
  font-weight: bold;
  border-radius: 50px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
}

// GUIDE PAGE --------------------------------------------------------------------------------------------
.guide-step {
  padding: 1em;
  margin: 1.5em auto;
  max-width: 37.5em; //600px
  img {
    width: 100%;
    height: auto;
    margin-bottom: 12.5em; //200px
  }
}

.guide-title {
  display: flex;
  justify-content: left;
  margin-top: 3.125em;
  margin-left: 3.125em;
  font-size: 1.5em;
}

.guide-titles{
  font-size: 2.5em;
}

.bee-guide-img {
  display: flex;
  justify-content: right;
  width: auto;
  height: 28vw;
  transform: rotate(40deg);
  opacity: 60%;
}

.guide-rose-icon {
  display: flex;
  justify-content: left;
  transform: scaleX(-1);
  transform: rotate(-20deg);
  margin-right: 62.5em;
  margin-left: 15.6em;
}

.guide-rose-icon-small {
  display: flex;
  justify-content: right;
  transform: rotate(25deg);
  margin-right: 18.7em;
}

.explore-btn::part(body) {
  color: #FFF4E1;
}

// POEMS GRID --------------------------------------------------------------------------------------------
.poems-grid{
  display: grid;
  grid-template-columns: repeat(2, 677px);
  grid-auto-rows: 3fr;
  justify-content: space-around;
  .poem-card{
    display: flex;
    margin: 0.5em;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
}

// POEMS DIALOG ------------------------------------------------------------------------------------------
.poem-dialog {
  --width: 50%;
}

.poem-dialog::part(body){
  margin-top: -5em;
}

.saveBtn {
  color: #fff4e1;
}

// DROPDOWN BTN ------------------------------------------------------------------------------------------
.dropbtn {
  background-color: transparent;
  padding: 0.36em;
  font-size: 2em;
  border: none;
  align-items: center;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  visibility: hidden;
  position: absolute;
  background-color: #f1f1f1;
  width: 46vw;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 0.75em 1em;
  text-decoration: none;
  display: block;
  border-radius: 50px !important;
}

// dropdown links on hover
.dropdown-content a:hover {background-color: #ddd;}

// dropdown menu on hover
.dropdown:hover .dropdown-content {visibility: visible; display: block;}

.row-dropdown-length {
  float: left;
  width: 33.3%;
  text-align: left;
}

.row-dropdown-updates {
  float: left;
  width: 33.3%;
  text-align: left;
}

.row-dropdown-C {
  float: right;
  width: 33.3%;
  text-align: left;
}

/* DROPDOWN LIST ITEMS --------------------------------------------------------------------------------------- */
.length-list {
  align-items: left;
  text-align: left;
  list-style-type: none;
}

.updates-list {
  align-items: left;
  text-align: left;
  list-style-type: none;
}

.C-list {
  align-items: left;
  text-align: left;
  list-style-type: none;
}

/* DROPDOWN FILTER BTNS -------------------------------------------------------------------------------------- */
.apply-filters {
  float: left;
  width: 33.3%;
  text-align: left;
  border: 1px #FEC76C;
  gap: 2em;
}

.reset-filters::part(base) {
  background-color: #FFF4E1;
  margin: none;
}

.btn {
  border: none;
  background-color: inherit;
  cursor: pointer;
  display: inline-block;
}

/* SEARCH GRID FILTER  ------------------------------------------------------------------------------------- */
.searchContainer {
  display: inline-flex;
  flex: 1 1 300px;
  border: 1px solid #ccc;
  border-radius: 50px;
  width: 70%;
  height: 7vh;
  margin: 0 auto;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  background-color: #FFFFFF;
  margin-top: 1em;
  margin-left: 1.2em;
  margin-bottom: 0.9em;
}

.searchBox {
  border: 0;
  padding: 0.5rem 0.5rem 0.5rem 0;
  flex: 1;
  background-color: transparent;
  margin: 1em;
}

.searchIcon {
  padding: 0.8rem;
}

.search-icon {
  margin: 1em;
  margin-left: 1em;
}

.search-title {
  font-size: 2.5em;
  margin: 1em;
  margin-left: 4em;
  font-weight: 100;
}

.searchButton {
  background: #FEC76C;
  border: none;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  color: white;
  margin: 0.3em;
  margin-right: 0.7em;
  border-radius: 10px;
  font-family: var(--base-font-family);
  padding: 0.3em;
  width: 5.3vw;
  font-size: 1em;
  font-weight: bold;
}

input.searchBox:focus-visible {
  outline: none !important;
 } 

.search-grid {
  display: grid;
  grid-template-columns: repeat(4, 300px);
  grid-auto-rows: 149px;
  gap: 0.5em;
  margin: 0 auto;
  width: fit-content;

  & sl-button:last-child{
    grid-column: 1/-1;
    justify-self: center;
    width: 25%;
  }
  
  & sl-button {
    display: flex;

    &::part(base) {
      height: auto;
    }

    &::part(label) {
      display: flex;
      align-items: center;
    }
  }
}

.search-grid-h2 {
  display: flex;
  justify-content: left;
  margin-left: 8em;
  margin-top: 2em;
  font-size: 1.2em;
  font-weight: 100;
}

.search-grid sl-button::part(base){
  background-color: transparent;
  border-radius: 20px;
  border: 0.05em solid black;
  margin: 1em;
  font-family: var(--base-font-family);
  font-weight: bold;
  font-size: 1.3em;
  color: black;
}

.feeling-adventurous-btn {
  display: flex;
  justify-content: center;
  text-decoration: underline;
  font-size: 1.3em;
}

.randomise-btn::part(base) {
  background-color: #FFF4E1;
  font-family: var(--base-font-family);
  font-weight: bold;
  font-size: 1.3em;
  color: black;
  border: 0.05em solid black;
}

.randomise-btn{
  width: 12em;
}

.randomise-container {
  display: flex;
  justify-content: center;
  margin: auto;
}

/* FILTER ICON ------------------------------------------------------------------------------------------------ */
.gg-options {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs,1));
  width: 0.6vw;
  height: 0.25vh;
  box-shadow:
    -3px 4px 0 0,
    3px -4px 0 0
}

.gg-options::after,
.gg-options::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  width: 0.5vw;
  height: 1vh;
  //border: 2px solid pink;
  border-radius: 100%
}
.gg-options::before {
  top: -7px;
  left: -4px
}
.gg-options::after {
  bottom: -7px;
  right: -4px
}

