.img-slider {
  position: relative;
  width: 100%;
  height: auto;
  margin: 10px;
}

.img-slider .slide {
  z-index: 1;
  position: absolute;
  width: 100%;
  clip-path: circle(0% at 0 50%);
}

.img-slider.fade-in img {
  clip-path: circle(100% at 0 50%) !important;
  transition: 0.5s;
  transition-property: clip-path;
}

.img-slider .slide img {
  height: 350px;
  aspect-ratio: 1 / 1.6;
}

.img-slider .navigation {
  display: flex;
  justify-self: center;
  margin-top: 10px;
}

